//  
:root {
  //  
  --main-font: "Oswald", sans-serif;
  --additional-font: "Arial", "Helvetica", sans-serif;

  //    
  --primary-color: #000000; //   
  --font-color-light: #ffffff; //   
  --bg-color-light: #ffffff; //   
  --font-color-dark: #444444; //   
  --bright-color: #68b738; //  
  --backgroun-main-color: #f2f2f2; //   
}

.page__body {
  height: 100%;
  margin: 0;
  box-sizing: border-box;
}

.global-wrapper {
  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    padding-bottom: 86px;
    width: 1220px;
    margin-right: auto;
    margin-left: auto;
    padding-top: 75px;
  }
}

